import "core-js/modules/es.regexp.exec.js";
import "core-js/modules/es.string.match.js";
import "core-js/modules/es.string.replace.js";
import "core-js/modules/es.array.sort.js";
import "core-js/modules/es.symbol.description.js";
import "core-js/modules/web.dom-collections.iterator.js";
import { errorPopup, successPopup } from "../../../shared/notifications.js";
import axios from "../../../shared/axios.js";
export default {
  data() {
    return {
      socket: null,
      cabinet: {
        options: [],
        loading: true
      },
      urgency: {
        options: [],
        loading: true
      },
      defects: {
        options: [],
        loading: true
      },
      disable: {
        clientName: false,
        clientPhone: false,
        cabinet: false,
        urgency: false,
        defects: false,
        description: false,
        submit: false
      },
      form: {
        clientName: "",
        clientPhone: "",
        cabinet: "",
        urgency: "",
        defects: [],
        description: ""
      },
      rules: {
        clientName: [{
          required: true,
          message: "Пожалуйста, введите ваше Ф.И.О.",
          trigger: "blur"
        }, {
          min: 5,
          message: "Ф.И.О. должно содержать более 4 символов",
          trigger: "blur"
        }],
        clientPhone: [{
          pattern: /\(\d{2}\) \d{3}-\d{2}-\d{2}/,
          message: "Телефон должен быть действительным",
          trigger: "blur"
        }],
        cabinet: [{
          required: true,
          message: "Пожалуйста, укажите кабинет",
          trigger: "change"
        }],
        urgency: [{
          required: true,
          message: "Пожалуйста, укажите срочность",
          trigger: "change"
        }],
        defects: [{
          required: true,
          message: "Пожалуйста, укажите неисправности",
          trigger: "change"
        }]
      }
    };
  },

  methods: {
    submitForm() {
      this.$refs.formRef.validate(valid => {
        if (valid) {
          this.toggleAll();
          this.sendData();
          return true;
        }

        return false;
      });
    },

    resetForm() {
      this.$refs.formRef.resetFields();
    },

    formatPhone() {
      const val = this.form.clientPhone.replace(/\D/g, "").match(/(\d{0,2})(\d{0,3})(\d{0,2})(\d{0,2})/);
      this.form.clientPhone = !val[2] ? val[1] : `(${val[1]}) ${val[2]}${val[3] ? `-${val[3]}` : ""}${val[4] ? `-${val[4]}` : ""}`;
    },

    toggleAll() {
      Object.keys(this.disable).forEach(key => {
        this.disable[key] = !this.disable[key];
      });
    },

    sendData() {
      const data = {
        client_name: this.form.clientName,
        client_phone: this.form.clientPhone,
        urgency: this.form.urgency,
        cabinet: this.form.cabinet,
        defects: this.form.defects.sort().join("; "),
        defect_description: this.form.description
      };
      axios.post("/request", data).then(() => {
        successPopup("Успешно отправлено");
        this.setLocalStorate();
        this.resetForm();
        this.getLocalStorage();
        this.toggleAll();
      }).catch(() => {
        errorPopup("Упс! Мы не смогли отправить заявку. Повторите попытку позже либо обратитесь к администратору");
        this.toggleAll();
      });
    },

    getLocalStorage() {
      this.form.clientName = localStorage.getItem("request-client-name");
      this.form.clientPhone = localStorage.getItem("request-client-phone");

      if (localStorage.getItem("request-urgency")) {
        this.form.urgency = localStorage.getItem("request-urgency");
      }

      if (localStorage.getItem("request-client-cabinet")) {
        this.form.cabinet = localStorage.getItem("request-client-cabinet");
      }
    },

    setLocalStorate() {
      localStorage.setItem("request-client-name", this.form.clientName);
      localStorage.setItem("request-client-phone", this.form.clientPhone);
      localStorage.setItem("request-urgency", this.form.urgency);
      localStorage.setItem("request-client-cabinet", this.form.cabinet);
    },

    getDefects() {
      return axios.get("/request/defects").then(response => {
        this.defects.options = response.data.map(el => ({
          value: el,
          label: el
        }));
        this.defects.loading = false;
        return true;
      });
    },

    getCabinets() {
      return axios.get("/request/cabinets").then(response => {
        this.cabinet.options = response.data;
        this.cabinet.loading = false;
        return true;
      });
    },

    getUrgencies() {
      return axios.get("/request/urgency").then(response => {
        this.urgency.options = response.data;
        this.urgency.loading = false;
        return true;
      });
    }

  },

  mounted() {},

  beforeMount() {
    Promise.all([this.getCabinets(), this.getDefects(), this.getUrgencies()]).then(() => {
      this.getLocalStorage();
    }).catch(() => {
      errorPopup("Упс! Мы не смогли загрузить данные. Пожалуйста, обратитесь к администратору.");
      this.toggleAll();
    });
  }

};